    /* default box shadow */
}

.icon__default
{
    display:    block;
    fill:       #42566e;
}

.icon__size__S
{
    width:  24px;
    height: 24px;
}

.icon__size__M
{
    width:  32px;
    height: 32px;
}

.icon__size__L
{
    width:  40px;
    height: 40px;
}

.icon__size__XL
{
    width:  48px;
    height: 48px;
}

.icon__theme__control:hover, .icon__theme__control.icon__fakeHovered, button:focus > .icon__theme__control, .icon__theme__light:hover, .icon__theme__light.icon__fakeHovered, button:focus > .icon__theme__light
    {
        fill:   #19a2e2;
    }

.icon__theme__button
{
    fill:   #ffffff;
}

.icon__theme__dark:hover, .icon__theme__dark.icon__fakeHovered, button:focus > .icon__theme__dark
    {
        fill:   #ffffff;
    }

.icon__theme__navigation
{
    /* not implemented */
}

:not(.icon__theme__button).icon__type__alert
    {
        fill:   #ff965a;
    }

:not(.icon__theme__button).icon__type__approved
    {
        fill:   #19a2e2;
    }

:not(.icon__theme__button).icon__type__approved-green
    {
        fill:   #1bbab0;
    }

:not(.icon__theme__button).icon__type__declined, :not(.icon__theme__button).icon__type__ended, :not(.icon__theme__button).icon__type__deactivated
    {
        fill:   #98a8bc;
    }

:not(.icon__theme__button).icon__type__error, :not(.icon__theme__button).icon__type__removed
    {
        fill:   #ff5460;
    }

:not(.icon__theme__button).icon__type__info
    {
        fill:   #19a2e2;
    }

:not(.icon__theme__button).icon__type__info-edit
    {
        fill:   #ff965a;
    }

:not(.icon__theme__button).icon__type__paused
    {
        fill:   #b1dc68;
    }

:not(.icon__theme__button).icon__type__pending
    {
        fill:   #ff965a;
    }

:not(.icon__theme__button).icon__type__validation
    {
        fill:   #1bbab0;
    }

.icon__disabled:not(.icon__theme__button)
{
    pointer-events: none;
    fill:           #c7d0db;
}

